<template>
  
	<v-row>
		<v-col>
			<v-sheet>
				<v-card
					flat
					tile
				>
					<v-window
						v-model="onboarding"
						reverse
					>
						<v-window-item
							v-for="(n, idx) in items"
							:key="`card-${idx}`"
						>
							<v-card
								height="500"
								:img="n.src"
							>
								<v-row
									class="fill-height"
									align="center"
									justify="center"
								>
									<h1
										style="font-size: 5rem;"
										class="white--text slide_text"
										elevation="4"
									>
										{{ n.이름 }}
									</h1>
								</v-row>
							</v-card>
						</v-window-item>
					</v-window>

					<v-card-actions class="justify-space-between">
						<v-btn
							text
							@click="prev"
						>
							<v-icon>mdi-chevron-left</v-icon>
						</v-btn>
						<v-item-group
							v-model="onboarding"
							class="text-center"
							mandatory
						>
							<v-item
								v-for="(n, idx) in items"
								:key="`btn-${idx}`"
								v-slot="{ active, toggle }"
							>
								<v-btn
									:input-value="active"
									icon
									@click="toggle"
								>
									<v-icon>mdi-record</v-icon>
								</v-btn>
							</v-item>
						</v-item-group>
						<v-btn
							text
							@click="next"
						>
							<v-icon>mdi-chevron-right</v-icon>
						</v-btn>
					</v-card-actions>
				</v-card>
			</v-sheet>
		</v-col>
	</v-row>
</template>

<script>
	import data from '@/assets/data/rooms.js';
  export default {
    data () {
			return {
				onboarding: 0,
				items: data
			}
		},

    methods: {
      next () {
        this.onboarding = this.onboarding + 1 === this.info.length
          ? 0
          : this.onboarding + 1
      },
      prev () {
        this.onboarding = this.onboarding - 1 < 0
          ? this.info.length - 1
          : this.onboarding - 1
      },
    },
  }
</script>

<style scoped>
	.slide_text{text-shadow:5px 5px 5px rgba(0,0,0,.3);}
</style>
